.footer {
    height: 150px;
    background-color: #b765da;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.name {
    background-color: #a4a29b;
}
.block {
    border: 1px solid darkblue;
    width: 300px;
    height: 40px;
    display: flex;
    justify-content: space-between;
}
.item {
    background-color: #a4a29b;
    width: 40px;
}
.allRights {
    background-color: #a4a29b;
}